import React, {useState, useEffect} from 'react'
import {connect} from 'react-redux'
import {Button, Card, Divider, Drawer, Form, Input, message, Modal, Select, Table} from "antd";
import {DeleteOutlined, EditOutlined, FilterOutlined, ReloadOutlined, SettingOutlined} from "@ant-design/icons";
import moment from "moment";
import utils, {findKey} from "../../../../../utils";
import ApiService from "../../../../../services/api.service";
import TypeStatusAirlinesForm from "../../../components/forms/type-status-airlines.form";


function TableClientLists({setQuery, query, ...props}) {
	let {list, onReload} = props
	const [loading, setLoading] = useState(false)
	const [loadings, setLoadings] = useState([])
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	function handleClickEdit(_, index) {
		setVisible(!visible)
		let newField = _ ?? {}
		form.resetFields()
		setSelected(newField)
		
	}
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				new ApiService({
					body: value,
					url: `/api/v1/thin-client/${selected?.id}`
				}).patch()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.info(JSON.stringify(err))
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)
		
	}
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/thin-client/${_}`,
		}).delete()
			.then((response) => {
				if (!response?.error) {
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						// setData([...data.filter((item) => item.id !== _)])
						setLoadings([])
					}, 2000)
				} else {
					enterLoading(index, false)
					setLoadings([])
					message.error(response?.message ?? "Error: delete by id " + _)
				}
				
			})
			.catch((err) => {
				enterLoading(index, false)
				setLoadings([])
			})
	}
	
	const onChangeDrawer = (val) => {
		setVisibleDrawer(val ?? false)
	}
	
	
	const defaultColumns = [
		{
			visible: true,
			key: "no",
			title: "No",
			dataIndex: "id",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'id'),
			render: (_, val, index) => index + 1,
			width: 70,
		},
		{
			visible: false,
			key: "identifier",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'identifier'),
			title: "Identifier",
			dataIndex: "identifier",
		},
		{
			visible: true,
			key: "name",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'display_name'),
			title: "Display Name",
			dataIndex: 'display_name',
		},
		{
			visible: true,
			key: "ip_address",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'ip_address'),
			title: "Ip Address",
			dataIndex: "ip_address",
		},
		{
			visible: true,
			key: "location",
			title: "Location",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'location'),
			dataIndex: "location",
		},
		{
			visible: true,
			key: "created_at",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'created_at'),
			title: "Created At",
			dataIndex: "created_at",
			render: (_) => _ ? moment(_).format("DD/MM/YYYY") : "-"
		},
		{
			visible: true,
			key: "updated_at",
			sorter: (a, b) => utils.antdTableSorter(a, b, 'created_at'),
			title: "Updated At",
			dataIndex: "updated_at",
			render: (_) => _ ? moment(_).format("DD/MM/YYYY") : "-"
		},
		{
			visible: true,
			key: 'action',
			title: "Action",
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2 justify-content-end'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							disabled={loadings.length > 0 ? true : _?.isDeleted ? true : list?.data.some((item) => {
								return typeof (item?.isDeleted) !== 'undefined' ? item?.isDeleted === true : false
							})}
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	
	
	function ExtraComponent() {
		return (
			<div className={'text-right d-flex align-items-center mk-gap-2'}>
				<Button
					size={'small'}
					ghost
					loading={list?.loading ?? false}
					onClick={onReload}
					type={'primary'}
					icon={<ReloadOutlined/>}
				/>
			</div>
		)
	}
	
	const onColumnsChange = (value) => {

	}
	return (
		<React.Fragment>
			<Drawer
				title={`Filter`}
				placement={"right"}
				closable
				onClose={() => onChangeDrawer(false)}
				visible={visibleDrawer}
			>
				<div className={'w-100'}>
					<h5>Table Column</h5>
					<Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
					        defaultValue={defaultColumns.filter((item) => item?.visible === true).map((item) => item?.key) ?? []}>
						{
							defaultColumns.filter((item) => item?.visible === true).map((item) => (
								<Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
							))
						}
					</Select>
					
					<Divider/>
				</div>
			</Drawer>
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{
					...selected,
				}} layout={'vertical'}>
					<Form.Item name={"identifier"} label={'Identifier'} hidden rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					
					<Form.Item
						name={"display_name"}
						label={'Display Name'}
						rules={[{required: true, message: "Can't be empty!"}]}
						hasFeedback
					>
						<Input/>
					</Form.Item>
					<Form.Item
						name={"ip_address"}
						label={'IP Address'}
						rules={[{required: true, message: "Can't be empty!"}]}
						hasFeedback
					>
						<Input/>
					</Form.Item>
					<TypeStatusAirlinesForm
						name={'location'}
						label={'Location'}
						keyValue={['data','identifier']}
						keyDisplay={['data','name']}
						select={{
							showSearch:true,
							placeholder:"Select your type"
						}}
						mapping={function({data}){
							return (
								data.map((item,index)=> (
									<Select.Option value={item?.identifier} key={item?.id}>{item?.identifier ?? findKey(item,['data','name']) ?? "undefined"}</Select.Option>
								))
							)
						}}
						rules={[
							{
								required:true,
								message: "Can't be empty!"
							}
						]}
					/>
				</Form>
			</Modal>
			<Card
				title={'Master Data'}
				bordered={false}
				extra={<ExtraComponent/>}>
				<Table
					loading={list?.loading ?? false}
					pagination={{
						pageSize: query?.limit ?? 10,
						current: query?.page ?? 1,
						total: list?.pagination?.total_items ?? 0,
						onChange: function (page, pageSize) {
							setQuery({
								...query,
								page,
								limit: pageSize
							})
						}
					}}
					columns={defaultColumns.filter((item) => item?.visible === true)}
					dataSource={list?.data ?? []}
				/>
			</Card>
		</React.Fragment>
	)
}

TableClientLists.propTypes = {}
TableClientLists.defaultProps = {}

export default connect(({MasterData}) => {
	let {thin_client} = MasterData
	let {list} = thin_client
	return {list}
}, {})(React.memo(TableClientLists))