import React, {useState,useEffect} from 'react'
import { connect } from 'react-redux'
import HeroComponents from "../../../../../components/layout-components/CustomrPageHeaderAlt";
import {Button} from "antd";
import ContainerTailwind from "../../../../../components/layout-components/ContainerTailwind";
import CustomTab from "../../../../../components/layout-components/custom.tab";
import TableClientLists from "./table-client.lists";
import ButtonPermission from "../../../../../components/permission-components/button.permission";
import {GET_LIST_MD_THIN_CLIENT} from "../../../../../redux/actions/MasterData";
import {useHistory} from "react-router-dom";
import {PlusOutlined} from "@ant-design/icons";
/**
 *
 * @param {Object} props
 * @param {Object} props.list
 * @param {Function} props.GET_LIST_MD_THIN_CLIENT
 * @returns {JSX.Element}
 * @constructor
 */
function ListPages(props){
    let { GET_LIST_MD_THIN_CLIENT , list} = props
  const history = useHistory()
    const [query, setQuery ] = useState({
        page:1,
        limit:10
    })

    useEffect(()=> {
        GET_LIST_MD_THIN_CLIENT({query})
    },[GET_LIST_MD_THIN_CLIENT,query])

    return (
        <React.Fragment>
            <HeroComponents
                loading={list?.loading ?? false}
                name={"Overview"}
                positionName={['Master Data','Thin Client']}
                thumbnail={'/overview.png'}
                extra={<>
                    <ButtonPermission
                        permission={['public','admin']}
                        theme={{
                            type:"disabled",
                            property:{
                                message:"Access Denied"
                            }
                        }}
                        config={{
                          onClick: function(){
                            history.push('/dashboard/master-data/thin-client/create')
                          },
                          ghost:true,
                          icon:<PlusOutlined/>,
                          // icon: PlusOutlined,
                            type:"primary",
                            size:"small",
                            loading:list?.loading ?? false
                    }}>
                        Add New
                    </ButtonPermission>
                </>}
            />

            <div style={{marginTop:90}}>
              <ContainerTailwind>
                <TableClientLists
                  query={query}
                  onReload={()=> GET_LIST_MD_THIN_CLIENT({query})}
                  setQuery={setQuery}/>
              </ContainerTailwind>
            </div>
        </React.Fragment>
    )
}

ListPages.propTypes = {}
ListPages.defaultProps = {}

export default connect(({MasterData})=> {
    let { thin_client } = MasterData
    let { list } = thin_client
    return { list }
},{GET_LIST_MD_THIN_CLIENT})(React.memo(ListPages))